import React, { useMemo, useRef } from 'react';
import { CardLayout, EntityPageConfig, LayoutContent } from '../../types';
import {
  EntityPage,
  EntityPageState,
  useEntity,
  useEntityPage,
  useEntityPageContext,
  MoreActions,
} from '@wix/patterns';
import { useForm } from '@wix/patterns/form';
import { Card, Cell, Layout } from '@wix/design-system';
import { useParams } from 'react-router-dom';
import { useSchema } from '../../providers/SchemaContext';
import { BaseInputRef } from './Fields/types';
import { FormFieldInput } from './Fields/FormFieldInput';
import { useEntityPageHeaderTexts, useNavigation } from '../../hooks';
import { usePatternsWizardOverridesContext } from '../../providers';
import { useEntityPageMoreActions } from '../../hooks/useEntityPageMoreActions';
import { useActionsSDK } from '../../hooks/useActionsSDK';

const RenderLayoutContent: React.FC<{
  content: LayoutContent;
  level: number;
  sectionId: string;
  cardIndex: number;
  contentIndex: number;
  setInputRef: (id: string, input: BaseInputRef) => void;
}> = ({ content, level, sectionId, cardIndex, contentIndex, setInputRef }) => {
  const schema = useSchema();
  const overrides = usePatternsWizardOverridesContext();
  const pageState = useEntityPageContext();

  switch (content.type) {
    case 'field':
      if (!content.field.fieldId) {
        return null;
      }

      const field = schema.fields[content.field.fieldId];

      if (!field) {
        return null;
      }

      return (
        <Cell span={content.field.span}>
          <FormFieldInput
            key={content.field.fieldId}
            field={field}
            inputRef={(ref) => setInputRef(content.field.fieldId, ref)}
          />
        </Cell>
      );

    case 'container':
      if (!content.container.children?.length) {
        return null;
      }

      return (
        <Cell span={content.container.span}>
          <Layout>
            {content.container.children.map((child, index) => (
              <RenderLayoutContent
                key={`${sectionId}-card-${cardIndex}-content-${contentIndex}-level-${level}-child-${index}`}
                content={child}
                level={level + 1}
                sectionId={sectionId}
                cardIndex={cardIndex}
                contentIndex={index}
                setInputRef={setInputRef}
              />
            ))}
          </Layout>
        </Cell>
      );

    case 'component':
      if (!content.component.componentId) {
        return null;
      }

      const Component = overrides?.components?.[content.component.componentId];

      if (!Component) {
        return null;
      }

      return (
        <Cell span={content.component.span}>
          <Component
            form={pageState.form as any}
            entity={pageState.entity as any as Record<string, any>}
          />
        </Cell>
      );

    default:
      return null;
  }
};

const RenderLayoutCard: React.FC<{
  layout: CardLayout;
  sectionId: string;
  cardIndex: number;
  setInputRef: (id: string, input: BaseInputRef) => void;
}> = ({ layout, sectionId, cardIndex, setInputRef }) => {
  switch (layout.type) {
    case 'card':
      return (
        <EntityPage.Card minHeight="50px">
          <Card.Header
            title={layout.card.title.text}
            subtitle={layout.card.subtitle?.text}
          />
          <Card.Divider />
          <Card.Content>
            <Layout>
              {layout.card.children.map((child, index: number) => (
                <RenderLayoutContent
                  key={`${sectionId}-card-${cardIndex}-content-${index}`}
                  content={child}
                  level={0}
                  sectionId={sectionId}
                  cardIndex={cardIndex}
                  contentIndex={index}
                  setInputRef={setInputRef}
                />
              ))}
            </Layout>
          </Card.Content>
        </EntityPage.Card>
      );
    default:
      return null;
  }
};

export interface AutoPatternsEntityPageProps {
  configuration: EntityPageConfig;
}

export const AutoPatternsEntityPage: React.FC<AutoPatternsEntityPageProps> = ({
  configuration,
}) => {
  const {
    layout: pageLayout,
    parentPageId,
    route,
    moreActions,
    collectionId,
  } = configuration;

  const form = useForm<any>({
    mode: 'onChange',
  });
  const params = useParams();
  const entityId = params[route.params.id];
  const schema = useSchema();
  const isCreateMode = !entityId;

  const inputRefs = useRef<{ [fieldId: string]: BaseInputRef }>({});

  const setInputRef = (id: string, input: BaseInputRef) => {
    inputRefs.current[id] = input;
  };

  const validate = () => {
    const invalidFieldId = Object.keys(inputRefs.current).find((fieldId) => {
      return inputRefs.current[fieldId]?.invalid;
    });

    if (invalidFieldId) {
      inputRefs.current[invalidFieldId]?.focus?.();
      throw new Error('Invalid form');
    }
  };
  const { getParentPagePath } = useNavigation();

  const parentPath = useMemo(
    () => getParentPagePath(parentPageId),
    [parentPageId, getParentPagePath],
  );

  const state: EntityPageState<any> = useEntityPage<any, any>({
    parentPath,
    form,
    onSave: async () => {
      validate();

      const formValues = form.getValues();
      const baseEntity = state.entity || {};
      const updatedEntity = {
        ...baseEntity,
        ...formValues,
      };

      const newEntity = entityId
        ? await schema.actions.update(updatedEntity)
        : await schema.actions.create(updatedEntity);

      return { updatedEntity: newEntity };
    },
    fetch: async () => {
      if (!entityId) {
        return { entity: undefined };
      }
      const entity = await schema.actions.get(entityId);
      return { entity };
    },
  });

  const entity = useEntity(state);

  const { title, subtitle } = useEntityPageHeaderTexts({
    config: configuration,
    isCreateMode,
    entityDisplayName: entity?.[schema.displayField],
  });

  const sdk = useActionsSDK({
    collectionId,
  });

  const resolvedMoreActions = useEntityPageMoreActions(
    moreActions,
    entity,
    sdk,
    form,
  );

  return (
    <EntityPage state={state} dataHook="auto-patterns-entity-page">
      <EntityPage.Header
        title={title}
        subtitle={subtitle}
        moreActions={
          resolvedMoreActions.length > 0 ? (
            <MoreActions items={resolvedMoreActions} />
          ) : undefined
        }
      />
      <EntityPage.Content>
        <EntityPage.MainContent dataHook="entity-page-main-content">
          {pageLayout?.main?.map((layout, layoutIndex) => (
            <RenderLayoutCard
              key={`main-section-card-${layoutIndex}`}
              layout={layout}
              sectionId="main"
              cardIndex={layoutIndex}
              setInputRef={setInputRef}
            />
          ))}
        </EntityPage.MainContent>
        {pageLayout?.sidebar ? (
          <EntityPage.AdditionalContent>
            {pageLayout.sidebar?.map((layout, layoutIndex) => (
              <RenderLayoutCard
                key={`sidebar-section-card-${layoutIndex}`}
                layout={layout}
                sectionId="sidebar"
                cardIndex={layoutIndex}
                setInputRef={setInputRef}
              />
            ))}
          </EntityPage.AdditionalContent>
        ) : null}
      </EntityPage.Content>
    </EntityPage>
  );
};

/*
  Key structure explanation:

  Main cards: main-section-card-{layoutIndex}
  Sidebar cards: sidebar-section-card-{layoutIndex}

  Layout content:
  {sectionId}-card-{cardIndex}-content-{contentIndex}

  Nested children:
  {sectionId}-card-{cardIndex}-content-{parentContentIndex}-level-{level}-child-{childIndex}

  This ensures uniqueness across all levels and sections.
*/
